<template>
    <div class="faxian" v-if="indexshow">
        <yd-slider autoplay="3000" style="height: 3.84rem">
            <yd-slider-item v-for="item in bannerlist" @click.native="gonewdetail(item.id)">
                <img :src="item.banner">
            </yd-slider-item>
        </yd-slider>
        <!-- -----------------APP应用下载开始------------------ -->
        <div class="dwon">
          <h1 class="ttile">{{$t('code.dwnlod')}}</h1>
          <div class="yd-grids-4">
            <div class="y-list">
              <div class="yd-grids-item" v-for="item in yiongyong" :id="item.id" @click="godetail()">
                  <div class="yd-grids-icon">
                    <img :src="item.iconPath">
                  </div>
                  <div class="yd-grids-txt">
                    <span>{{item.appName}}</span>
                    <span>{{item.appEngName}}</span>
                  </div>
              </div>
              <div class="clear"></div>
            </div>
          </div>
        </div>
        <div class="bj"></div>
        <!-- *****************消息列表开始******************* -->
        <div class="news_list">
          <h2 class="ttile">{{$t('code.newsTile')}}</h2>
        </div>
        <div class="list">
          <yd-flexbox slot="list" class="pro_list_item" v-for="item in list" @click.native="gonewdetail(item.id)">
                <!-- <div  class="pro_list_img">
                    <img src="../assets/images/new.png">
                </div> -->
                <yd-flexbox-item  class="new_title">
                    <h1 v-html="item.title"></h1>
                    <!-- <div class="span" v-html="item.content" style="font-size: .24rem"></div> -->
                </yd-flexbox-item>
                <div  class="new_time">
                    <span>{{item.createTime}}</span>
                </div>
            </yd-flexbox>
            <div v-if="pagenum > 6">
              <div class="loaddata" v-if="loadtext == $t('data.loadmore')" @click="loadList()">{{loadtext}}</div>
              <div class="loaddata" v-if="loadtext == $t('data.Nomoredata')">{{loadtext}}</div>
            </div>
            
        </div>
        <!-- 尾部 -->
        <footerBar :footerdata="footerdata"></footerBar>
    </div>
  
</template>

<script>
  import footerBar from './footer.vue'
export default {
  components:{
      "footerBar":footerBar,
  },
  data () {
    return {
      footerdata:{
        bj:'#fdfdff'
      },
      indexshow:false,
      yiongyong:[],
      page:1,
      pagesize:6,
      pagenum:0,
      list:[],
      loadtext:this.$t('data.loadmore'),
      bannerlist:[]
    }
  },
  created(){
    this.loadList()
    this.yingyong()
    this.banner()
    
  },
  methods:{
    yingyong(){
      var that = this
      this.axios({
        url:this.Url + 'App.ashx',
        method:'post',
        params: {req:4,opentype:1}
      }).then(res=>{
        var _list = res.data.data
        if (res.data.success) {
          for (var i = 0; i < _list.length; i++) {
            _list[i].iconPath = that.imgUrl + _list[i].iconPath
          }
          that.yiongyong = _list
        }
      }).catch(err => {})
    },
    banner(){
      var that = this
      this.axios({
        url:this.Url + 'News.ashx',
        method:'post',
        params: {req:6}
      }).then(res=>{
        var _list = res.data.data
        if (res.data.success) {
          for (var i = 0; i < _list.length; i++) {
            _list[i].banner = that.imgUrl + _list[i].banner
            that.bannerlist.push(_list[i])
          }
        }
      }).catch(err => {})
    },
    godetail(){
      this.$dialog.toast({
          mes: '我们正在努力开发中...',
          timeout: 1500
      });
    },
    loadList() {
      this.openloading(this.$t('toast.loding'))
      var that = this;
      this.axios({
        url:this.Url + 'News.ashx',
        method:'post',
        params: {req:4,title:'',pageindex:this.page,pagecount:this.pagesize}
      }).then(function (res) {
          that.closeloading()
          var _list = res.data.data;
          if (res.data.data.length == 0) {
            that.loadtext = that.$t('toast.nodata')
              that.$dialog.toast({
                  mes: that.$t('toast.nodata'),
                  timeout: 1500
              });
              return;
          }else{
            for (var i = 0; i < _list.length; i++) {
              _list[i].title = _list[i].title.length>14?_list[i].title.substring(0,14)+"...":_list[i].title
              _list[i].content = _list[i].content.length>18?_list[i].content.substring(0,18)+"...":_list[i].content
              _list[i].createTime = _list[i].createTime.split("T")[0]
            }
            that.list = [...that.list, ..._list];
            that.pagenum = res.data.totalCount
            that.page++;
          }
          
      }).catch(function(res){
          that.closeloading()
          res = JSON.stringify(res)
          if(JSON.stringify(JSON.parse(res).request)=="{}"){
              that.catchErr()
              setTimeout(function(){
                  that.$dialog.confirm({
                      title: that.$t('data.networkerr')+'...',
                      opts: (res) => {
                          that.loadList() 
                      }
                  });
              },2000)
          }
      });
    },
    //跳转新闻详情
    gonewdetail(id){
      this.$router.push({path:'/newdetail',query:{id:id}})
    }
  },
  
  mounted(){
    this.indexshow=true
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import '../assets/css/faxian.css'
</style>



